import { api as index } from "..";

const API_URL = process.env.NEXT_PUBLIC_API;
const API_ENDPOINT = process.env.NEXT_PUBLIC_ENDPOINT;

namespace OLYMPIANS {
    export interface IOlympian {
        id: number;
        student: {
            name: string;
            surname: string;
        };
        name_of_olympia: {
            choosing: string;
            choosing_ky: string;
            choosing_ru: string | null;
        };
    }

    export type GetOlympiansResponse = IOlympian[];
    export type GetOlympiansRequest = void;
    export type GetDetOlympianTypeResponse = IOlympian[];
    export type GetDetOlympianTypeRequest = string | number | void;
}

const api = index.injectEndpoints({
    endpoints: (build) => ({
        getOlympians: build.query<
            OLYMPIANS.GetOlympiansResponse,
            OLYMPIANS.GetOlympiansRequest
        >({
            query: () => ({
                url: `${API_URL}/${API_ENDPOINT}/students/olympians/`,
                method: "GET",
            }),
            providesTags: ["olympians"],
        }),
        getOlympiansSelection: build.query<
            OLYMPIANS.GetDetOlympianTypeResponse,
            OLYMPIANS.GetDetOlympianTypeRequest
        >({
            query: (id) => ({
                url: `${API_URL}/${API_ENDPOINT}/students/olympians/olymp_categories/${id}/`,
                method: "GET",
            }),
        }),
    }),
});

export const { useGetOlympiansQuery, useGetOlympiansSelectionQuery } = api;
